.toggle {

    .bb-checkbox {
        position: relative;

        &__title {
            font-family: $f-medium;
            font-size: 16px;
            color: #c5c5c5;

            &.is-checked {
                color: $gray--dark;
            }
        }
    }

    .bb-checkbox--slide input:checked + .bb-checkbox__box {
        background-color: $gray--lighten;
    }
} //.toggle
