$widget-colors: (
    new: $color-info,
    work: $color-warning,
    done: #90bb61,
    abort: #e44a66
);

.request {
    background-color: #fff;

    &__inner {
        @include row-flex;

        .widget {
            border: 0;
            box-shadow: 0 2px 5px 0 rgba(164, 173, 186, 0.25);
            @include col;
            @include size(3);
            @include size-md(12);

            @include respond-to(md) {
                box-shadow: none;
                border: 0;
            }

            @include respond-to(xs) {
                margin: 0;
                width: 100%;
                border-left: 0;
                border-right: 0;
                box-shadow: none;
            }

            &__body {
                border-left: 1px solid #dee3e7;
                border-right: 1px solid #dee3e7;
                border-bottom: 1px solid #dee3e7;
                border-radius: 0 0 $border-radius $border-radius;

                @include respond-to(xs) {
                    border: 0;
                }
            }
        } //.request__inner .widget
    } //.request__inner

    &__date {
        margin-left: auto;
        margin-right: 0;

        @include respond-to(xs) {
            margin-left: 0;
        }
    } //.request__date

    &__widget {
        @each $theme, $color in $widget-colors {

            &--#{$theme} {

                .widget {

                    &__header {
                        color: #fff;
                        background-color: $color;
                    }
                }

                .request-item {

                    &__date {
                        @include respond-to(xs) {
                            border-left: 4px solid $color;
                        }
                    }

                    &__icon {
                        color: $color;
                    }
                }
            }
        }

        .widget {

            &__header {
                text-align: center;
                border-bottom: 0;
                justify-content: center;

                @include respond-to(md) {
                    display: none;
                }
            }

            &__body {
                @include respond-to(md) {
                    padding: 0;
                    box-shadow: none;
                    border: 0;
                    @include row-flex;
                }

                @include respond-to(xs) {
                    padding: 0;
                }
            }
        }
    } //.request__widget

    &__tab {
        @include respond-min(xs) {
            @include row-flex;
            align-items: flex-start;
        }

        @include respond-to(xs) {
            margin-left: -10px;
            margin-right: -10px;
        }

        .request__widget {
            margin-bottom: 0;
            @include col;
            @include size(3);

            @include max-screen(1199) {
                width: 100%;
            }

            @include respond-to(xs) {
                width: 100%;
                margin-left: 0;
                margin-right: 0;
                box-shadow: none;
                border: 0;
                background-color: transparent;
            }
        } //.request__tab .request__widget
    } //.request__tab

    &__empty {
        @include flexCenter;
        flex-direction: row;

        .empty {

            &__icon {
                margin-right: $gutter;
            }
        }
    } //.request__empty

    .tab {

        &__titles {
            margin-bottom: $gutter;

            @include respond-min(mdX) {
                display: none;
            }

            @include respond-min(xs) {
                @include col;
                @include size(12);
            }

            @include respond-to(xs) {
                margin: 0 10px 20px;
            }
        } //.request .tab__titles

        &__title {
            margin: 0 7px;

            &:first-child {
                margin-left: 0;
            }

            &:last-child {
                margin-right: 0;
            }
            @each $theme, $color in $widget-colors {

                &--#{$theme} {

                    .tab {

                        &__icon {
                            color: $color;
                        }
                    }

                    &.ui-tabs-active,
                    &:hover {

                        .tab {

                            &__link {
                                border-bottom-color: $color;
                            }

                            &__icon {
                                color: $color;
                            }
                        }
                    }
                }
            }
        } //.request .tab__title

        &__link {
            font-family: $f-medium;
            font-size: 15px;
        } //.request .tab__link
    } //.request .tab

