.request-item {
    display: block;
    margin-bottom: 10px;
    padding: $gutter / 2;
    border: 1px solid $color-border--lightest;
    border-radius: $border-radius * 2;
    background-color: #fff;
    cursor: pointer;
    transition: initial;
    // overflow: hidden;
    position: relative;
    @include size-md(6);
    @include size-xs(12);

    &:last-child {
        margin-bottom: 0;
    }

    @include respond-to(md) {
        box-shadow: 0 2px 5px 0 rgba(164, 173, 186, 0.25);
        @include col;
    }

    @include respond-to(xs) {
        margin-bottom: 0;
        padding: 0;
        border-left: 0;
        border-right: 0;
        border-radius: 0;
        box-shadow: none;
    }

    & + & {
        @include respond-to(xs) {

            &:not(.request-item--notfilled) {
                border-top: 0;
            }
        }
    }

    &:first-child {
        margin-top: 0;
    }

    &__inner {
        @include respond-to(xs) {
            margin-left: 60px;
            padding: 10px;
            // border: 1px solid $color-border--lightest;
            border-left: transparent;
            display: flex;
            flex-direction: column;
        }
    } //.request-item__inner

    &__link {
        color: $color-text;
        // @include coverdiv;

        @include respond-to(sm) {
            // display: none;
        }
    } //.request-item__link

    &__title {
        margin-right: 15px;
        margin-bottom: 0;
        font-family: $f-medium;
        font-size: 15px;
        color: $gray--darkest;
    } //.request-item__title

    &__phone {
        display: block;
        font-family: $f-num--b;
        font-size: 12px;
        color: $color-text;
    } //.request-item__phone

    &__service {
        display: block;
        margin: 5px 0;
        font-family: $f-i;
        font-size: 14px;
        line-height: 1;
        color: #969696;

        @include respond-to(xs) {
            @include text-overflow;
        }
    } //.request-item__service

    &__master {
        display: block;
        margin-bottom: 8px;
        font-size: 14px;
        line-height: 1;
        color: $color-text;

        @include respond-to(xs) {
            margin-bottom: 0;
            margin-right: 65px;
            @include text-overflow;
        }
    } //.request-item__master

    &__place {
        @extend .request-item__master;
        font-family: $f-i;
        color: $color-text;
    } //.request-item__place

    &__info {
        font-size: 14px;
        @include flex;
        align-items: center;
    } //.request-item__info

    &__price {
        font-family: $f-bold;
        font-size: 14px;
        color: $color-text;

        @include respond-to(xs) {
            position: absolute;
            right: 11px;
            bottom: 7px;
        }

        i {
            font-size: 12px;
        }
    } //.request-item__price

    &__date {
        margin-left: auto;
        font-family: $f-i;
        font-size: 13px;
        color: #969696;
        float: right;

        @include respond-to(xs) {
            padding: 5px 10px;
            font-size: 14px;
            color: $gray--darkest;
            text-align: center;
            background-color: transparent;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            span {
                display: block;
                padding: 5px 0;

                &:first-child {

                    span {
                        display: none;
                    }
                }

                &:last-child:not(:only-child) {
                    border-top: 1px solid $color-border--light;
                }
            }

            i {
                display: none;
            }
        }
    } //.request-item__date

    &__icon {
        line-height: 1;
        position: absolute;
        top: 11px;
        right: 10px;

        .fa-info-circle {
            color: $color-danger;
        }
    } //.request-item__icon

    &--new {
        border-color: transparent;
        background-color: #f5f5f5;

        .request-item {

            &__icon {
                display: none;
            }
        }
    } //.request-item--new

    &--notfilled {
        border: 1px solid $color-danger;

        & + .request-item--notfilled {
            @include respond-to(xs) {
                margin-top: 1px;
                border-top: 0;
            }
        }

        @include respond-to(xs) {
            border-left: 0;
            // border-top: 1px solid $color-danger !important;
        }
    } //.request-item--notfilled

    &--user {

        .request-item {

            &__date {
                color: #969696;
                justify-content: flex-start;
            }
        }
    } //.request-item--user

    &.drag-sort {
        border: 0;
        box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.3);
    } //.request-item.drag-sort
} //.request-item
