.studio {

    &__wrap {
        @include respond-to(xs) {
            margin-top: 20px;
        }

        .button-box {
            margin-bottom: $gutter;
            @include flex;

            .btn {
                margin-left: auto;
            }
        }
    } //.studio__wrap

    &__items {
        // box-shadow: $bxs-box;
        // background-color: #fff;
        // position: relative;
        border: 1px solid $color-border--lightest;
        box-shadow: $bxs-box-two;
        @include flexColumn;

        // @include respond-to(sm) {
        //     // background-color: transparent;
        //     // box-shadow: none;
        // }

        @include respond-to(xs) {
            margin: 0 -10px;
            border-top: 0;
            border-left: 0;
            border-right: 0;
            box-shadow: none;
        }
    } //.studio__items

    &__item {
        // border-bottom: 1px solid $color-border--lightest;

        // &:last-child {
        //     border-bottom: 0;
        // }

        // @include respond-to(sm) {
        //     margin-bottom: $gutter;
        //     box-shadow: $bxs-box;
        //     border-bottom: 0;
        // }

        // @include respond-to(xs) {
        //     margin-bottom: 0;
        //     box-shadow: none;
        //     border-top: 1px solid $color-border--lightest;

        //     &:last-child {
        //         border-bottom: 1px solid $color-border--lightest;
        //     }
        // }
    } //.studio__item
} //.studio

.studio-item {

    .row {
        margin: 0;
        padding: 10px 15px 9px 25px;
        position: relative;

        @include respond-to(sm) {
            padding: 10px 15px;
        }

        @include respond-to(xs) {
            padding-left: 20px;
        }
    }

    &:not(.studio-item--header) {
        background-color: #fff;
        border-top: 1px solid $color-border--lightest;

        @include respond-min(sm) {
            cursor: pointer;

            &:hover {
                // background-color: $color-bg--lightest;
                background-color: $color-gray--hover;

                .studio-item {

                    &__controls {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }
        }
    }

    &--header {
        // background-color: $color-bg--light;
        background-color: #fff;

        @include respond-to(sm) {
            display: none;
        }

        .text {
            font-family: $f-medium;
            font-size: 15px;
            color: $gray--darkest;
        }

        .studio-item__inner {
            margin-bottom: 0;
        }

        .row {
            padding: 16px 15px 14px 25px;
        }
    } //.studio-item--header

    &__inner {
        color: $gray;
        @include col;
        @include size(4);
        @include size-sm(12);
        @include flexCenter;

        @include respond-to(sm) {
            font-size: 15px;
            font-family: $f-medium;
            flex-wrap: wrap;
            align-items: flex-start;

            &:last-child {
                margin-bottom: 0;
            }
        }

        @include respond-to(xs) {
            margin-bottom: 5px;
            margin-left: 3px;
        }

        &:nth-child(2) {
            @include size-md(5);
            @include size-sm(12);

            @include respond-to(sm) {
                margin-bottom: 0;
                margin-left: 7px;

                .text {
                    font-size: 14px;
                    font-family: $f-light;
                    color: $gray--darkest;
                }
            }

            @include respond-to(xs) {
                margin-left: 0;
            }
        }

        &:nth-child(3) {
            @include size-md(3);
            @include size-sm(12);

            @include respond-to(sm) {

                .studio-item__link {
                    display: none;
                }
            }
        }
    } //.studio-item__inner

    &__title {
        display: block;
        width: 100%;
        margin-bottom: $gutter / 2;
        color: $gray--darkest;
        font-size: 15px;
        font-family: $f-bold;

        @include respond-min(sm) {
            display: none;
        }
    } //.studio-item__title

    &__icon {
        width: 16px;
        height: 16px;
        margin-right: 5px;
        margin-top: 2px;
        color: $gray--lighten;
        fill: $gray--light;
    } //.studio-item__icon

    &__preview-list {
        @include respond-to(sm) {
            margin-top: 10px;
        }
    } //.studio-item__preview-list

    &__controls {
        margin-left: auto;
        opacity: 0;
        visibility: hidden;
        @include centre('y');
        right: 0;

        @include respond-to(sm) {
            display: block;
            margin-left: 0;
            opacity: 1;
            visibility: visible;

            .control {

                &__item {
                    margin: 0;
                }
            }

            .info-block {
                margin: 0;
                padding: 10px;
                // position: absolute;
                // top: 5px;
                // right: 0;
                // z-index: z(fixed) - 1;

                &::before {
                    transform: rotate(0);
                }

                &.is-active {
                    z-index: z(fixed);
                }
            }
        }
    } //.studio-item__controls

    &__photo {
        font-family: $f-medium;
        color: $gray;

        &:hover {
            color: $gray--darkest;
            text-decoration: none;

            .btn-link__icon {
                fill: $gray--darkest;
            }
        }

        .btn-link {

            &__icon {
                width: 16px;
                height: 16px;
                margin-right: 5px;
                fill: $gray--light;
                position: relative;
                top: -1px;
                @include tr;
            }
        }
    } //.studio-item__photo

    &__link {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 40px;

        @include respond-min(xs) {
            display: none;
        }
    } //.studio-item__link

    &__user {

        .info-block {
            padding: 0;
        }

        .bb-dropdown {

            &:hover {

                .bb-dropdown {

                    &__list {
                        top: 22px;
                    }
                }
            }

            &__list {
                top: 32px;
            }
        }
    } //.studio-item__user

    .user {

        &__inner {
            @include flexCenter;
        } //.studio-item .user__inner

        &__img {
            width: 50px;
            height: 50px;
            margin-right: $gutter;

            @include respond-to(sm) {
                width: 40px;
                height: 40px;
                margin-right: 0;
                @include centre('y');
                left: 10px;
            }
        } //.studio-item .user__img

        &__name {
            font-family: $f-medium;
            color: $gray;
            line-height: 1.5;
        } //.studio-item .user__name

        &__color {
            position: absolute;
            top: 0;
            left: 0;
            width: 4px;
            height: 100%;

            @each $theme, $color in $text-colors {

                &.#{$theme} {
                    background-color: $color;
                }
            }
        } //.studio-item .user__color

        &__text {

            .info-block {
                @include respond-to(xs) {
                    margin-top: 5px;
                }
            }
        } //.studio-item .user__text
    } //.studio-item .user

    .info-block {
        margin: 0 0 0 2px;
        font-family: $f;

        &::before {
            margin-left: 2px;

            @include respond-to(sm) {
                margin-left: 0;
            }
        }
    } //.studio-item .info-block
} //.studio-item
