.move-block {
    @include flexColumn;
    @include visibleBlock;

    &__header {
        height: 50px;
        padding: 0 20px;
        font-size: 18px;
        color: $gray--darkest;
        // @include border(bottom);
        border-bottom: 1px solid $color-border--lightest;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        @include flexCenter;
        justify-content: center;

        .select2-container--default {

            &.select2-container--open {

                .select2-selection--single {
                    border-bottom: 1px solid;
                }
            }

            .select2-selection--single {
                border-bottom: 0;

                .select2-selection__arrow b {
                    top: 0;
                }

                .select2-selection__rendered {
                    font-family: $f;
                    font-size: 17px;
                }
            }
        }
    } //.move-block__header

    &__body {
        background-color: $color-bg--lighten;
        overflow-x: hidden;
        @include bb-scroll--thin;
        flex-grow: 1;
        position: absolute;
        top: 50px;
        bottom: 0;
        left: 0;
        right: 0;
    } //.move-block__body

    &__footer {
        padding: 20px;
        // background-color: $color-bg--lightest;
        border-top: 1px solid $color-border--lightest;
        // @include border(top);
        background-color: #fff;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        @include flexColumn;
        align-items: center;

        .text {
            margin-bottom: 10px;
        }

        .button-box {
            width: 100%;
            @include flexCenter;
            justify-content: center;
        }

        .btn {
            width: 100%;
            padding-left: 15px;
            padding-right: 15px;

            & + .btn {
                margin-left: 10px;
            }
        }
    } //.move-block__footer

    &__close {
        width: 38px;
        height: 38px;
        padding: 10px;
        cursor: pointer;
        @include tr;
        @include vy;
        right: 0;

        &:hover {
            fill: $gray;
        }
    } //.move-block__close

    &__inner {
        background-color: #fff;
    } //.move-block__inner

    &__item {
        min-height: 50px;
        margin: 5px 0;

        &--btn {
            font-family: $f-medium;
            font-size: 16px;
            cursor: pointer;
        } //.move-block__item--btn
    } //.move-block__item

    &__box {
        @include visibleBlock;
    } //.move-block__box

    &__icon {
        width: 22px;
        height: 22px;
        margin-right: 10px;
    } //.move-block__icon

    &__date {
        font-family: $f-medium;
    } //.move-block__date

    &__validate {
        text-align: center;
        display: none;
        position: absolute;
        top: -65px;
        left: 10px;
        right: 10px;
    } //.move-block__validate

    &--mobile {

        .move-block {

            &__header {
                padding: 0;

                .bb-input {
                    width: 100%;
                }

                .select2-container {
                    height: 50px;
                    line-height: 50px;
                    padding: 0 15px;
                }

                .select2-container .select2-selection--single {
                    height: 49px;
                    line-height: 52px;

                    .select2-selection__rendered {
                        padding-right: 0;
                        height: 50px;
                        line-height: 52px;
                        text-align: center;

                        span {
                            position: relative;

                            &::after {
                                content: '\f107';
                                @include fa('light');
                                margin-left: 5px;
                            }
                        }
                    }

                    .select2-selection__arrow b {
                        display: none;
                    }
                }

                .select2-container--default.select2-container--open.select2-container--below
                .select2-selection--single:hover {
                    border: 0;
                } //.move-block--mobile .move-block__header .select2-container--default.select2-container--open.select2-container--below .select2-selection--single:hover
            }
        }

        .user {
            cursor: pointer;

            &__phone {
                color: $gray--light;
            }

            &__icon {
                padding: 10px 13px;
                font-size: 20px;
                color: $color-text;
                @include centre('y');
                right: 0;
            } //.move-block .user__icon
        } //.move-block .user

        .aplication-item {
            margin-top: 10px;
            padding: 15px 20px;
            box-shadow: none;
            // @include border(top);
            // @include border(bottom);
            border-top: 1px solid $color-border--lightest;
            border-bottom: 1px solid $color-border--lightest;
            border-radius: 0;

            @include respond-to(xs) {
                padding: 16px;
            }

            &__row {
                margin-bottom: 15px;
                justify-content: center;
            }

            &__col {
                @include size-sm(12);

                & + .aplication-item__col {
                    margin-top: 10px;
                }
            }

            &__service {
                margin-bottom: 0;
            }

            &__btn {

                &--edit {
                    margin: auto;

                    @include respond-to(xs) {
                        padding: 5px 0;
                        font-size: 14px;
                    }
                } //.move-block .aplication-item__btn--edit
            } //.move-block .aplication-item__btn
        } //.move-block--mobile .aplication-item

        .aplication-info {
            padding: 15px 10px;

            &__wrap {
                overflow-y: auto;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 123px;
            }
        } //.move-block .aplication-info

        .search-client {
            display: block;
            height: 100%;
            background-color: #fff;

            &__form {
                padding: 10px;
                @include flexCenter;

                .search {
                    margin-right: 15px;
                }
            }

            &__icon {

                &--add {
                    padding: 10px;
                    cursor: pointer;
                } //.move-block--mobile .search-client__icon--add
            } //.move-block--mobile .search-client__icon

            .btn {
                // display: block;
                // width: 100%;
                // padding-top: 12px;
                // padding-bottom: 9px;
                // font-size: 17px;
                // background-color: #fff;
                // border-bottom: 1px solid $color-border--lightest;
                // border-radius: 0;

                display: block;
                margin: 7px auto 0;
                padding: 6px 15px;
                font-size: 14px;
                color: #fff;
                background-color: $color-main;
                border-radius: 50px;
                box-shadow: 0 2px 15px #a5a5a596;

                &:hover {
                    text-decoration: none;
                }
            } //.move-block--mobile .search-client .btn

            .user {
                padding: 10px;
                border-bottom: 1px solid $color-border--lightest;
            } //.move-block .search-client .user

            .aplication__in {
                // padding-top: 35px;
                background-color: #fff;
                position: absolute;
                top: 66px;
                left: 0;
                right: 0;
                bottom: 0;
            } //.move-block .search-client .aplication__in
        } //.move-block--mobile .search-client

        .new-client {
            z-index: inherit;
            @include visibleBlock;

            &__form {
                min-height: 100%;
                background-color: #fff;
            } //.move-block .new-client__form

            .bb-text {
                margin-bottom: 0;

                @include respond-to(xs) {
                    margin-bottom: 25px;
                }
            } //.move-block .new-client .bb-text

            .move-block {

                &__body {
                    background-color: #fff;
                } //.move-block .new-client .move-block__body
            } //.move-block .new-client .move-block

            .select2-container--default {

                .select2-selection--single {
                    border-bottom: 1px solid $color-border--light;
                }
            }
        } //.move-block .new-client

        &.aplication-success {
            position: inherit;

            .move-block {

                &__body {
                    overflow: hidden;
                } //.aplication-success .move-block__body
            } //.aplication-success .move-block
        } //.move-block .aplication-success

        .aplication-success {

            &__user {
                margin-bottom: 5px;
                padding: 10px;
                background-color: #fff;
            } //.move-block--mobile .aplication-success__user

            &__tab {
                padding: 0;

                .tab {
                    background-color: #fff;

                    &__titles {
                        padding: 9px 10px 0;
                        // @include border(top);
                        border-top: 1px solid $color-border--lightest;
                    }

                    &__content {
                        background-color: #fff;
                        top: 115px;
                        bottom: auto;

                        &#aplication-chat {
                            bottom: 0;
                            border-bottom: 0;
                        }

                        &#aplication-history,
                        &#aplication-info {
                            height: 100%;
                        }

                        &#aplication-history {

                            .box-inline__wrap {
                                padding: 15px 10px;
                                overflow-y: auto;
                                position: absolute;
                                top: 0;
                                left: 0;
                                right: 0;
                                bottom: 123px;
                            }
                        }
                    }

                    &__inner {
                        padding: 15px 10px;
                    }

                    &__link {
                        padding: 5px 0 11px;
                    }
                } // .aplication-success .tab
            } // .move-block--mobile .aplication-success__tab
        }

        &.aplication-comments {

            .move-block {

                &__body {
                    background-color: #fff;
                }

                &__inner {
                    padding: 20px 10px;
                }
            }
        } //.aplication-success

        .bb-date {
            width: 100%;
            margin: 0;

            &__title {
                margin-bottom: 0;
            }

            &__input {
                font-size: 16px;

                &:not(.move-block__date) {
                    font-family: $f;
                }

                &:focus {
                    box-shadow: none;
                }
            }
        } //.aplication-success .bb-date

        .select2-container {

            .select2-selection--single {
                height: 37px;
                line-height: 37px;

                .select2-selection__rendered {
                    padding-left: 0;
                    padding-right: 25px;
                    line-height: 37px;
                    font-size: 16px;

                    .custom-select__results {

                        span {

                            &:first-child {
                                @include text-overflow;
                                flex-grow: 1;
                            }

                            &:not(:first-child) {
                                font-size: 14px;
                            }

                            &:nth-child(2) {
                                margin-left: auto;
                            }

                            &:nth-child(3) {
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
        } //.aplication-success .select2-container

        .select2-container--default
        .select2-selection--single[aria-expanded='false'] {

            &:hover {
                border: 0;
            }
        }

        .select2-container--default {

            &.select2-container--open,
            &.select2-container--open.select2-container--below {

                .select2-selection--single {
                    border-bottom: 1px solid $color-border--lighten;
                }
            }

            .select2-selection--single {
                border-left: 0;
                border-right: 0;
                border-top: 0;
                border-bottom: 1px solid transparent;
                border-radius: 0;
                box-shadow: none;
            }
        } //.aplication-success .select2-container--default

        .select2-container--open .select2-dropdown--below {
            border-left: 0;
            border-right: 0;
        } //.move-block--mobile .select2-container--open .select2-dropdown--below
    } //.move-block--mobile

    &.is-invalid {

        .move-block {

            &__item {

                &.is-error {
                    border-bottom: 1px solid $color-danger;
                }
            }

            &__validate {
                display: block;
            }
        }
    } //.move-block.is-invaid

    .user {

        &__name {
            font-family: $f-medium;
            font-weight: 500;
        }
    }
} //.move-block

.move-block-item {
    padding: 10px;
    background-color: #fff;
    position: relative;

    // @include border(top);
    // @include border(bottom);
    border-top: 1px solid $color-border--lightest;
    border-bottom: 1px solid $color-border--lightest;
    @include flexCenter;

    @include respond-to(sm) {
        cursor: pointer;
    }

    &__client {
        padding-left: 10px;
        @include coverdiv;
        @include flexCenter;
        flex-grow: 1;

        &::after {
            content: '';
            @include fa('light');
            @include centre('y');
        }

        &--add {

            &::after {
                content: '\f067';
                font-size: 14px;
                right: 14px;
            }
        } //.move-block-item__client--add

        &--selected {
            display: none;
            flex-grow: 1;
        } //.move-block-item__client--selected
    } //.move-block-item__client

    &__icon {
        margin-left: auto;
        font-size: 18px;
        @include centre('y');
        right: 14px;

        @include respond-to(xs) {
            font-size: 22px;
        }
    } //.move-block-item__icon

    &--client {

        &.is-selected {
            height: 60px;

            .move-block-item__client {
                display: none;

                &--selected {
                    display: flex;
                }
            }
        }
    } // .move-block-item--client

    &--date {
        padding: 0;

        input {
            height: auto;
            padding: 13px;
            border: 0;
            background-color: transparent;
        }
    } // .move-block-item--date

    &--wide {
        padding: 0;

        .select2-container .select2-selection--single {
            height: 50px;
        } // .move-block-item--wide .select2-container .select2-selection--single

        .select2-container
        .select2-selection--single
        .select2-selection__rendered {
            height: 50px;
            padding: 0 25px 0 10px;
        } // .move-block-item--wide.move-block-item--wide .select2-container .select2-selection--single .select2-selection__rendered

        .select2-container--default
        .select2-selection--single
        .select2-selection__arrow
        b {
            top: 6px;
        }
    } // .move-block-item--wide

    .select-color {
        width: 100%;

        select {
            @include respond-to(sm) {
                border: 0;
            }
        }
    } //.move-block-item .select-color

    .select2-container--default
    .select2-selection--single
    .select2-selection__arrow
    b {
        right: 14px;

        &::before {
            font-size: 22px;
        }
    }
} // .move-block-item

.move-block-service {
    width: 100%;
    @include flexCenter;

    &__info {
        @include flexColumn;
        flex-grow: 1;
    } //.move-block-service__info

    &__time {
        font-size: 14px;
    } //.move-block-service__time

    &__title {
        font-family: $f-medium;
        color: $gray--darken;
    } //.move-block-service__title

    &__duration {

        .text-time {
            font-size: 14px;

            i {
                margin-right: 5px;
                color: $color-icon--light;
            } //.move-block-service__duration .text-time i
        } //.move-block-service__duration .text-time

        .text-master {
            font-family: $f-i;
            font-size: 15px;
        } //.move-block-service__duration .text-master
    } //.move-block-service__duration

    &__price {

        i {
            margin-left: 5px;
            font-size: 14px;
            color: $color-icon--light;
        } //.move-block-service__price i
    } //.move-block-service__price
} //.move-block-service

.move-select {
    background-color: #fff;
    @include tr;
    @include flexColumn;

    &.is-active {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: z(overlay);

        .move-select {

            &__body,
            &__footer {
                display: flex;
            }

            &__search {

                &::after {
                    display: none;
                }

                i {
                    display: block;
                    left: -5px;
                }
            }

            &__field {
                height: auto;
                padding: 15px 25px 15px 59px;
                border-bottom: 1px solid $color-border--lighten !important;
            }
        }
    } //.move-select.is-active

    &__search {
        position: relative;

        &::after {
            content: '\f107';
            @include fa('light');
            font-size: 22px;
            color: $color-icon;
            @include vy;
            right: 0;
        }

        i {
            display: none;
            padding: 16px;
            @include vy;
            left: 10px;
        }
    } //.move-select__search

    &__field {
        width: 100%;
        height: 37px;
        padding: 0 20px 0 0;
        font-size: 16px;
        line-height: 1;
        border-left: 0;
        border-right: 0;
        border-top: 0;
        border-bottom: 1px solid $color-border--light;
        border-radius: 0;
        box-shadow: none;
        @include text-overflow;

        @include placeholder {
            padding: 0;
        }

        &:focus,
        &:hover {
            border-left: 0;
            border-right: 0;
            border-top: 0;
            box-shadow: none;
            border-radius: 0;
        }
    } //.move-select__field

    &__body {
        display: none;
        background-color: #fff;
        @include bb-scroll--thin;
        position: absolute;
        top: 50px;
        left: 0;
        right: 0;
        bottom: 85px;
        flex-direction: column;
        flex-grow: 1;
    } //.move-select__body

    &__footer {
        display: none;
        padding: 20px;
        @include border(top);
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;

        .btn {
            margin: auto;
            min-width: 180px;
            width: 100%;
        } //.move-select__footer .btn
    } //.move-select__footer

    &__results {
        background-color: #fff;
    } //.move-select__result

    &__result {
        padding: 7px 10px;

        & + & {
            @include border(top);
        }

        &.is-selected {
            background-color: $color-bg--lighten;
        }

        .user {

            &__img {
                width: 40px;
                height: 40px;
            }
        }
    } //.move-select__result
} //.move-select

.item-info {

    &__title {
        margin-bottom: 5px;
        color: $gray--darkest;
        font-family: $f-medium;
        font-size: 15px;

        span {
            position: relative;

            & + span {

                &::before {
                    margin-right: 5px;
                    margin-left: 1px;
                    content: '\f067';
                    @include fa('regular');
                    font-size: 11px;
                    color: $color-icon;
                }
            }
        }
    } //.item-info__title

    &__desc {
        font-size: 14px;
        @include flexCenter;
    } //.item-info__desc

    &__time {
        flex-grow: 1;

        i {
            color: $color-icon;
        }
    } //.item-info__time

    &__price {

        i {
            font-size: 12px;
            color: $color-icon;
        } //.item-info__price i
    } //.item-info__price
} //.item-info
