$widget-colors: (
    new: rgb(119, 182, 231),
    work: rgb(255, 217, 99),
    done: rgb(151, 204, 100),
    abort: rgb(253, 90, 62)
);

.widget {
    position: relative;
    margin-bottom: 30px;
    @extend %boxLight;
    @include flexColumn;

    @include respond-to(xs) {
        @include resetBox;
        margin-bottom: 0;
        margin-top: 20px;
    }

    &__wrap {
        @include row-flex;
        align-items: flex-start;

        .widget {
            @include col;
            @include size(3);
            @include size-md(4);
            @include size-sm(6);
            @include size-xs(12);

            &--md {
                @include size(6);
                @include size-md(6);
                @include size-sm(12);
            }
        }

        .card-item {
            @include col;
            @include size(6);
        }
    } //.widget__wrap

    &__header {
        @extend %cardHeader;
        @include flexCenter;

        @include respond-to(xs) {
            padding: 10px;
            border-bottom: 0;
        }

        .inline-select {
            margin-left: auto;
        }
    } //.widget__header

    &__title {
        color: $gray--darkest;

        @include respond-to(xs) {
            font-family: $f;
            font-size: 16px;
            border-bottom: 0;
        }
    } //.widget__title

    &__body {
        min-height: 50px;
        padding: 15px;

        @include respond-to(xs) {
            min-height: auto;
            padding: 10px;
        }
    } //.widget__body

    &__inner {
        @include flex;

        &--column {
            @include flexColumn;

            @include respond-to(xs) {
                padding: 10px 0;
            }

            .bb-checkbox {
                @include respond-to(xs) {
                }

                & + .bb-checkbox {
                    margin-top: 15px;
                }
            }
        } //.widget__inner--column

        .widget {

            &__left {
                width: 50%;
                position: relative;

                .list {

                    &__item {
                        justify-content: flex-end;
                    }

                    &__text {
                        margin-left: 0;
                        margin-right: 10px;
                    }
                }

                @include respond-to(xs) {
                    padding: $gutter $gutter / 2;

                    .list {

                        &__item {
                            margin: 0 0 $gutter / 2;

                            &:last-child {
                                margin-bottom: 0;
                            }
                        }
                    }
                }
            } //.widget__left

            &__right {
                width: 50%;
                margin-left: 40px;
                position: relative;

                @include respond-to(xs) {
                    padding: $gutter $gutter / 2;
                }

                &::before {
                    content: '';
                    display: block;
                    height: 80%;
                    width: 1px;
                    background-color: $color-bg--light;
                    @include vy;
                    left: -20px;

                    @include respond-to(xs) {
                        left: -10px;
                    }
                }
            } //.widget__right
        }
    } //.widget__body

    &__scroll {
        height: 282px;
        overflow: auto;
        @include flexColumn;

        @include respond-to(xs) {
            height: 278px;
        }

        .empty {
            margin: auto;
        }
    } //.widget__scroll

    &__items {
        min-height: 110px;
        @include flexCenter;
        flex: 1;

        @include respond-to(xs) {
            min-height: auto;
            padding-bottom: 15px;
        }

        &--column {
            @include flexColumn;

            @include respond-to(xs) {
                margin: 0 10px;
            }

            .widget__item {
                width: 100%;
                text-align: left;
                color: $color-text;
                align-items: flex-start;

                @include respond-to(xs) {
                    padding: 10px 0;
                }

                & + .widget__item {
                    border-top: 1px solid $color-border--lightest;
                }
            } //.widget__item
        } //.widget__items--column

        &--devider {

            .widget__item {

                &:not(:first-child) {
                    position: relative;

                    &::before {
                        content: '';
                        display: block;
                        height: 100%;
                        width: 1px;
                        background-color: $color-bg--light;
                        position: absolute;
                        left: 0;
                        top: 0;
                    }
                }
            }
        } //.widget__items--devider
    } //.widget__items

    &__item {
        padding: 15px;
        width: 50%;
        height: 100%;
        text-align: center;
        @extend %textLight;
        font-size: 15px;
        color: $color-primary;
        background-color: transparent;
        border: none;
        cursor: pointer;
        position: relative;
        @include tr;
        @include flexColumn;
        align-items: center;

        &:first-child {

            &::after {
                display: none;
            }
        }

        &.color-black {
            color: $gray--darken;

            &:hover {
                color: $gray--darken;
            }
        }

        @each $theme, $color in $widget-colors {

            &.is-#{$theme} {

                .widget__status {
                    color: $color;
                    border-color: $color;
                }
            }
        }

        span {
            display: block;
            margin: auto;
            font-family: $f-medium;
            font-size: 35px;
            line-height: 1;
        }

        img {
            width: 80%;
            padding: 5px 0;
        }

        small {
            display: block;
            margin-top: -3px;
            font-size: 12px;
        }
    } //.widget__item

    &__status {
        padding: 0 10px;
        font-size: 13px;
        border: 1px solid $color-border--lightest;
        border-radius: 15px;
        position: absolute;
        top: 11px;
        right: 0;
    } //.widget__status

    &__btn {

        &:hover {
            // color: lighten($color-primary--light, 7%);

            &::after {
                display: none;
            }

            & + .widget__item {

                &::after {
                    display: none;
                }
            }

            .widget__btn_top {
                // fill: lighten($color-primary--light, 7%);
                @include trfY(-5px);
            }
        }

        &_top {
            display: block;
            margin: auto;
            width: 28px;
            height: 28px;
            fill: $color-main;
            @include tr;
        } //.box__btn_top
    } //.widget__btn

    &__toggle {
        padding: 20px;
        @include flexCenter;
        justify-content: center;

        @include respond-to(xs) {
            padding: 0 10px 10px;
        }

        .bb-checkbox {

            &__box {
                top: 0;
            }
        }
    } //.widget__toggle

    &__time {
        font-size: 15px;
        font-family: $f-medium;

        @include respond-to(sm) {
            font-size: 16px;
        }
    } //.widget__time

    &__desc {
        font-family: $f-i;
        font-size: 14px;
        color: $color-text--light;

        @include respond-to(sm) {
            font-size: 16px;
        }

        span {
            display: block;
            margin: 5px 0 3px;
            font-size: inherit;
            font-family: $f-medium;
            color: $color-text;

            @include respond-to(xs) {
                font-family: $f;
            }
        }
    } //.widget__desc

    &__result {
        display: flex;
    } //.widget__result

    &__number {
        font-family: $f-medium;
        font-weight: 500;
        margin-left: auto;
        color: #000;
    } //.widget__number

    &__popup {
        display: none;
        position: fixed;
        top: 50%;
        left: 50%;
        width: 300px;
        background-color: #fff;
        box-shadow: $bxs;
        z-index: 50;
    } //.widget__popup

    &--icon {

        .widget {

            &__body {
                @include flexCenter;

                @include respond-to(xs) {
                    flex-wrap: wrap;
                }
            }
        }

        .icon {

            &__list {
                flex-grow: 1;

                @include respond-to(xs) {
                    margin-bottom: $gutter;
                }
            }
        }

        .btn {
            min-width: 160px;
            margin-left: $gutter;

            @include respond-to(xs) {
                min-width: 100%;
                margin: auto;
            }
        }
    } //.widget--icon

    .info-block {
        margin-left: auto;
        padding: 8px;

        &::before {
            position: relative;
            top: 2px;
        }

        &--right {

            &.is-active,
            &:hover {

                .info-block__dropdown {
                    top: 24px;
                }
            }

            .info-block {

                &__dropdown {
                    top: 33px;
                    right: -5px;
                }
            }
        }

        .bb-dropdown__list {
            left: -2px;
        }

        &[data-dropdown-position^='right'] {

            .bb-dropdown__list {
                left: auto;
                right: -2px;
            }
        }
    } //.widget .info-block

    .list {

        &.list-color--success {

            .list {

                &__icon {
                    color: $color-success;
                }
            }
        }
    } //.widget .list
} //.widget

.h50 {
    display: flex;
    flex-direction: column;

    .widget {
        @include respond-min(xs) {
            flex-basis: calc(50% - 30px);
        }
    }
}
