.page-cabinet {

    .container {
        @include respond-to(lg) {
            padding: 0;

            &:not(.container--sm) {
                max-width: 100%;
                margin: 0;
                padding: 0;
            }
        }
    } //.page-cabinet .container

    .header {
        // @include border(bottom);
        // background-color: rgba(251, 251, 251, 0.9);
        background-color: rgba(#fff, 0.9);
        box-shadow: 0 0px 3px rgba(0, 0, 0, 0.2);
        position: fixed;
        left: $base-header-size;
        right: 0;
        top: 0;
        z-index: z(dropdown);
        transition: transform ease-out 0.3s, width ease-out 0.3s,
            left 0.3s cubic-bezier(0.25, 0.8, 0.4, 0.95);

        &.is-moving {

            .header__title {
                @include trfX(190px);
            }
        }

        @include respond-to(xs) {
            width: 100%;
            left: 0;
        }

        &__top,
        &__inner {
            height: $base-header-size;
            @include respond-to(xs) {
                height: $base-header-size--small;
            }
        }

        &__title {
            transition: transform ease-out 0.3s;
        }

        &__inner {
            width: 100%;
            border-bottom: 0;
            @include respond-to(xs) {
                display: flex;
                align-items: center;
            }
        }

        &__logo {
            @include min-screen(426) {
                display: none;
            }
        }

        &.bg--dark {

            .header-action__request,
            .header-user {
                border-right: 0;
            }

            .header-action__request-info {
                border: 0;
            }

            .header-user {

                &__img {
                    border: 2px solid $color-border--darken;
                }
            }

            // .toggle-mnu {
            //     @include respond-to(xs) {
            //         display: block;

            //         span {
            //             background-color: #fff;

            //             &::before,
            //             &::after {
            //                 background-color: #fff;
            //             }
            //         }
            //     }
            // }

            .request-info__icon {
                @include respond-to(xs) {
                    color: #fff;
                }
            }
        }
    } //.page-cabinet .header

    .header-user {

        &[data-dropdown-position^='right'] {

            .bb-dropdown {

                &__list {
                    right: 10px;

                    &::before,
                    &::after {
                        right: 32px;

                        @include respond-to(sm) {
                            right: 12px;
                        }

                        @include landscape {
                            right: 12px;
                        }
                    }
                }
            }
        }
    } //.page-cabinet .header-user

    .header-action {
        @include respond-to(xs) {
            border-left: none;
        }

        &__fav,
        &__request {
            width: $base-header-size;
            height: $base-header-size;
            @extend %xs50;

            &:hover {
                background-color: rgba($color-bg--lighten, 0.45);

                &::before {
                    color: $gray--darkest;
                }
            }
        }

        &__request {
            margin-left: auto;
            border-left: 1px solid $color-border--lightest;
            border-right: 1px solid $color-border--lightest;

            &-info {
                border-left: 1px solid $color-border--lightest;
                border-right: 1px solid $color-border--lightest;
            }
        }
    } //.page-cabinet .header-action

    .request {

        &__items {
            @include respond-to(xs) {
                height: calc(100vh - 127px - 50px);
            }
        }

        .bb-dropdown {

            &__list {
                @include dropdownResponseWidth;

                @include respond-to(xs) {
                    border-top-color: $color-border--lightest;
                    border-left: 0;
                    border-right: 0;
                    border-bottom: 0;
                    right: -50px;
                    width: 425px;

                    &::after,
                    &::before {
                        right: 69px;
                    }
                }
            }
        }
    } //.page-cabinet .request

    .toggle-menu {
        margin-left: 0;

        @include respond-min(xs) {
            display: none;
        }
    } //.page-cabinet .toggle-mnu

    .btn-fixed {

        &[data-position='bottom right'] {
            right: 50px;
            bottom: 50px;

            @include landscape {
                right: 15px;
                bottom: 15px;
            }

            @include respond-to(xs) {
                right: 10px;
                bottom: 60px;
            }
        } //.page-cabinet .btn-fixed[data-position='bottom right']
    } //.page-cabinet .btn-fixed

    &.menu-open {

        .cabinet {
            width: calc(100% - 225px);
            margin-left: 248px;
        }
    } //.page-cabinet.menu-open

    &.mobile-nav--open {

        .cabinet {
            pointer-events: none;
        }
    } //.page-cabinet.mobile-nav--open

    &.menu-hidden {

        .menu {
            display: none;
        }

        .header {
            left: 0;
        }

        .cabinet {
            margin-left: 0;
        }
    } //.page-cabinet.menu-hidden

    .info-block {

        &[data-dropdown-position^='right'] {
            @include screen(480, 768) {

                .bb-dropdown {

                    &__list {
                        left: 70px;
                    }
                }
            }
        }
    }
} //.page-cabinet

.cabinet {
    width: calc(100% - #{$base-header-size});
    height: calc(100% - #{$base-header-size});
    margin-top: $base-header-size;
    margin-left: $base-header-size;
    padding: $gutter * 1.5 $gutter * 1.5;
    background-color: $color-bg--lightest;
    overflow: hidden;
    position: relative;
    transition: margin-left ease-out 0.3s, width ease-out 0.3s;

    @include respond-to(md) {
        padding: $gutter 10px;
    }

    @include respond-to(xs) {
        width: 100%;
        height: 100%;
        margin: $base-header-size--small 0;
        padding-top: 0;
    }

    &.bg--light {
        background-color: $color-bg--lightest;
    }

    &.full-screen {

        .album-item,
        .bb-upload__item {
            @include size(2);
            @include size-lg(3);
            @include size-sm(4);
            @include size-xs(6);

            @include respond-to(i7) {
                width: 100%;
                margin: 0 0 20px;
                box-shadow: none;
                border-radius: 0;
                border-bottom: 1px solid $color-border--lightest;
            }

            &__img {
                @include respond-to(i7) {
                    border-radius: 0;
                }
            }
        }
    }

    &.one-screen {
        padding: 0;
        overflow: hidden;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        @include flexColumn;
        flex: 1 1 auto;
    }

    &.no-padding {
        padding-left: 0;
        padding-right: 0;
        padding-bottom: 0;

        .top-line {
            padding: 0 30px;
        }
    }

    &__title {
        margin-bottom: 17px;
        font-family: $f-medium;
        font-size: 18px;
        @include flexCenter;

        @include min-screen(426) {
            display: none;
        }

        @include respond-to(xs) {
            font-size: 22px;
        }

        .btn {
            margin-left: auto;
            padding: 7px 15px;
        }
    } //.cabinet__title

    &__logo {
        display: none;
        width: 165px;
        height: 100%;
        margin-right: auto;
        align-items: center;
        justify-content: center;

        @include respond-to(xs) {
            margin-top: 3px;
            @include flex;
            flex: 1;
        }

        &__link {
            color: #fff;
            @include respond-to(xs) {
                color: $gray--darkest;
            }
        }
    } //.cabinet__logo

    &__tab {
        @include respond-to(xs) {
            margin: 0 -10px 0;
            padding: 20px 10px 0;
            background-color: #fff;
        }

        .tab {

            &__titles {
                margin-bottom: $gutter;
            }
        } //.cabinet__tab .tab

        .button-box {
            margin-bottom: $gutter;
            @include flexCenter;

            @include respond-to(xs) {
                margin: 0 10px;
            }
        } //.cabinet__tab .button-box

        .profile-editor {

            &__inner {
                @include respond-to(xs) {
                    padding-top: 0;
                }
            }
        }
    } //.cabinet__tab

    &__toggle {

        .widget {
            margin-bottom: 7px;

            &__inner {
                @include respond-to(xs) {
                    padding: 0;
                }
            } //.cabinet__toggle .widget__inner

            &__body {
                @include respond-to(xs) {
                    min-height: auto;
                    padding: 10px;
                }
            } //.cabinet__toggle //.widget__body

            &__left {
                @include respond-to(xs) {
                    padding: 0;
                }
            } //.cabinet__toggle .widget__left

            &__right {
                @include respond-to(xs) {
                    padding: 0;
                    margin-left: 20px;
                }
            } //.cabinet__toggle .widget__right
        } //.cabinet__toggle .widget

        .list {

            &__item {
                @include respond-to(xs) {
                    align-items: flex-start;
                }
            }

            &__icon {
                @include respond-to(xs) {
                    font-size: 17px;
                }
            } //.cabinet__toggle .list__icon

            &__text {
                @include respond-to(xs) {
                    font-size: 13px;
                    line-height: 1.2;
                }
            }
        } //.cabinet__toggle .list

        .bb-checkbox--toggle {

            .bb-checkbox {

                &__title {
                    font-family: $f-medium;
                } //.bb-checkbox--title
            } //.bb-checkbox
        } //.bb-checkbox--toggle
    } //.cabinet__toggle
} //.cabinet

