.icon {

    &__list {
        @include flexCenter;
    }

    &__item {
        max-width: 75px;
        margin: auto;
    }
} //.icon

.card-item {
    @extend %box;

    @include respond-to(sm) {
        margin-bottom: 40px;
    }

    @include respond-to(xs) {
        @include resetBox;
    }

    &__wrap {
        @include flex;
        @include respond-to(sm) {
            flex-wrap: wrap;
        }
    } //.card-item__wrap

    &__header {
        @extend %cardHeader;

        @include respond-to(i7) {
            font-size: 15px;
        }

        i {
            margin-right: 10px;
        }
    } //.card-item__header

    &__body {
        @include flexColumn;

        @include respond-to(xs) {
            padding: $gutter / 2;
        }

        p {
            display: block;
            margin-bottom: 15px;
            @extend %textLight;
            font-size: 14px;
        }
    } //.card-item__body

    &__box {
        padding: 20px 15px;
        border-bottom: 1px solid $color-border--lightest;
        @include flexColumn;

        @include respond-to(xs) {
            padding: 0;

            &.mb-17 {
                margin-bottom: 10px;
            }
        }

        .card-item {

            &__subtitle {
                font-size: 14px;
                color: $gray;
            } //.card-item__box .card-item__subtitle
        } //.card-item__box .card-item

        .bb-input {
            margin-right: $gutter;
            flex: 1;

            @include respond-to(md) {

                &:not(.bb-input--phone) {
                    margin-bottom: $gutter / 2;
                    width: 100%;
                }
            }

            @include respond-to(sm) {
                margin-right: 0;
            }

            @include respond-to(i5) {

                &::before {
                    right: 10px;
                }
            }

            &__wrap {
                @include flex;
                align-items: flex-start;

                @include respond-to(sm) {
                    flex-wrap: wrap;
                    justify-content: flex-start;
                }

                & + .bb-input__wrap {
                    margin-top: $gutter;
                }
            } //.card-item__box .bb-input__wrap

            &__title {
                position: static;
                min-width: 160px;
                margin-top: 9px;
                font-family: $f-medium;
                font-size: 16px;
                color: #000;

                @include respond-to(xs) {
                    margin-top: 9px;
                }

                @include respond-to(i5) {
                    min-width: max-content;
                    margin-right: $gutter / 2;
                }
            } //.card-item__box .bb-input__title

            &__input {
                width: 100%;

                @include respond-to(i5) {
                    padding: 9px 20px 9px 10px;
                }
            } //.card-item__box .bb-input__input

            &__message {
                top: 44%;
                right: 0;
                left: 0;

                @include respond-to(md) {
                    top: 100%;
                }
            } //.card-item__box .bb-input__message

            &--phone {
                max-width: 425px;
            } //.card-item__box .bb-input--phone
        } //.card-item__box .bb-input

        &.no-border {
            border-bottom: 0;
        } //.card-item__box--clear

        &--first {
            padding-top: 0;
        } //.card-item__box--first

        &--vk {

            .bb-input {
                max-width: 350px;
                margin-right: auto;

                &__title {
                    min-width: 75px;
                    margin-top: 9px;
                    font-family: $f-medium;
                }
            }
        } //.card-item__box--vk
    } //.card-item__box

    &__title {
        margin-bottom: 10px;
        font-size: 14px;
    } //.card-item__title

    &__caption {
        margin-bottom: 10px;
        font-family: $f;
        font-size: 16px;
        color: #000;
        @include flexCenter;

        span {
            line-height: 1;
        }

        .ico {
            margin-right: 10px;
        }
    } //.card-item__title

    &__social {
        margin-right: 185px;
        @include flexCenter;

        @include respond-to(lg) {
            margin-right: 195px;
        }

        @include respond-to(md) {
            margin-right: 0;
        }

        @include respond-to(xs) {

            .btn-social {
                max-width: 45px;
                min-width: 45px;
                padding: 10px 0;
            }
        }

        .bb-input {
            margin-right: 0;

            &__wrap {
                flex: 1;
            }
        }
    } //.card-item__social

    &__btn {
        min-width: 175px;

        @include respond-to(xs) {
            width: 100%;
        }
    } //.card-item__btn

    .social {
        margin-right: $gutter;

        &__inner {
            max-width: 170px;
            @include flexCenter;

            @include respond-to(i7) {
                max-width: 100%;
            }
        }
    } //.card-item .social

    .btn-social {
        width: max-content;
        text-transform: initial;

        @include respond-to(i7) {
            width: 100%;
        }

        & + .btn-social {
            margin-left: 7px;
        }
    } //.card-item .btn-social

    .button-box {
        margin-bottom: 0;
        @include flexColumn;

        @include respond-to(sm) {
            width: 100%;
        }

        .btn {
            margin: auto;
            max-width: 175px;

            @include respond-to(xs) {
                max-width: 100%;
            }

            & + .btn {
                margin-top: $gutter / 2;
            }
        }
    } //.card-item .button-box

    .bb-accordeon {
        @include respond-to(xs) {
            @include border(top);
        }

        &__inner {
            padding-bottom: $gutter;
            @include border(bottom);
        } //.card-item .bb-accordeon__inner

        &__item {
            border-bottom: 1px solid $color-border--lighten;
            border-radius: 0;

            &:last-child {
                border-bottom: 0;
            }

            &.is-open {
                border-bottom-color: $color-border--lightest;
            }

            & + .bb-accordeon__item {
                margin-top: 0;
            }
        } //.card-item .bb-accordeon__item

        &__title {
            padding: 18px 15px;
            font-size: 15px;
            color: $gray;
            background-color: $color-bg--lightest;

            &::before {
                color: $gray;
                @include fa('light');

                @include respond-to(xs) {
                    right: 11px;
                }
            }

            @include respond-to(xs) {
                padding: 10px;
            }
        } //.card-item .bb-accordeon__title

        &__content {
            padding: 15px 15px 20px;
            border-top: 1px solid $color-border--lightest;

            @include respond-to(xs) {
                padding: 10px;
            }
        } //.card-item .bb-accordeon__content

        .bb-input--title,
        .bb-text--title {
            margin-right: 0;
        } //.card-item .bb-accordeon

        .card-item__box {
            padding: 0 0 20px;

            &:last-child {
                padding-bottom: 0;
                border-bottom: 0;
            }
        } //.card-item .bb-accordeon
    } //.card-item .bb-accordeon
} //card

.box {
    @include respond-to(xs) {
        margin: 0 -10px;
        border-top: 0;
        border-left: 0;
        border-right: 0;
        box-shadow: none;
    }

    &__inner {
        @include flexColumn;
    } //.box__inner

    &__item {
        padding: 15px;

        @include respond-to(xs) {
            padding: 10px;
        }
    } //.box__item

    &--shadow {
        @extend %box;
    } //.box--shadow

    .request-info {

        &__item {
            position: static;
        }

        &__inner {
            padding: 0;
            position: relative;
            overflow-x: initial;
        }

        &__action {
            display: block;
            @include centre('y');
            margin: 0;

            @include respond-to(xs) {
                right: -10px;
            }
        }
    }
} //.box

.box-inline {
    @extend %box;
    @include flexColumn;

    &__header {
        @extend %headerLkStyle;
    } //.box-inline__header

    &__in {
        padding: $gutter;
        @include flex;

        @include respond-to(xs) {
            padding: $gutter $gutter / 2;
        }
    } //.box-inline__body

    &__text {
        line-height: 1.2;

        &--right {
            margin-left: auto;
        } //.box-inline__text--right

        i {
            font-size: 11px;
        }
    } //.box-inline__text

    &__title {
        margin-right: 15px;
        font-family: $f-medium;
        font-size: 15px;
        line-height: 1.2;
        color: $gray--darkest;
    } //.box-inline__title

    &__subtitle {
        padding-left: 15px;
        color: $gray--light;
        font-size: 14px;
        white-space: nowrap;
        position: relative;

        &::before {
            content: '';
            display: block;
            height: 100%;
            width: 1px;
            background-color: $gray--light;
            @include vy;
            left: 0;
        }
    } //.box-inline__subtitle

    &__controls {
        margin-left: auto;
    } //.box-inline__control

    &__list {
        padding-left: 10px;
        border-left: 3px solid $color-border--light;
        @include flexColumn;

        &.is-success {
            border-color: $color-success;
        }

        &.is-abort {
            border-color: $color-danger;
        }

        & + & {
            margin-top: $gutter;
        }
    } //.box-inline__list

    &__item {
        @include flexColumn;

        & + & {
            margin-top: 5px;
        }
    } //.box-inline__item

    &__line {
        font-size: 15px;
        line-height: 1.3;
        @include flexCenter;

        &--bottom {
            font-family: $f-light-i;
            font-size: 14px;
            color: $gray--light;
        }

        .box-inline__text {
            line-height: 1.5;
        }
    } //.box-inline__line
} //.box-inline

.box-with-img {
    @include row-flex;
    align-items: center;

    &__left {
        @include col;
        @include size(8);
        @include size-xs(12);
    } //.box-with-img__left

    &__right {
        @include col;
        @include size(4);
        @include size-xs(12);
    } //.box-with-img__right

    &__img {
        max-width: 170px;
        margin: 0 auto;

        @include respond-to(xs) {
            margin: $gutter auto $gutter * 2;
        }
    } //.box-with-img__img
} //.box-with-img

.top-line {
    margin-bottom: 30px;
    position: relative;
    @include flex;
    align-items: flex-start;

    @include respond-to(md) {
        margin-bottom: 0;
    }

    @include respond-to(xs) {
        margin: 20px 0;
    }

    &__inner {
        @include flexCenter;
        // flex-grow: 1;

        @include respond-to(xs) {
            width: 100%;
        }

        .top-line__date {
            margin-bottom: 0;
        }
    } //.top-line__inner

    &__sorting {
        position: relative;
        display: flex;
        flex-grow: 1;

        @include respond-to(md) {
            flex-wrap: wrap;
        }

        @include respond-to(xs) {
            width: 100%;
            flex-direction: column;
            align-items: flex-start;
        }

        .btn--back {
            height: 40px;
            line-height: 40px;
        }

        .control-box {
            @include respond-to(xs) {
                margin-top: 10px;
            }
        }
    } //.top-line__sorting

    &__studio-select {
        min-width: 300px;

        .bb-input {
            width: 100%;
        }
    } //.top-line__studio-select

    &__search {
        // margin-right: $gutter * 2;

        // @include respond-to(md) {
        //     width: 50%;
        //     margin-bottom: $gutter;
        //     margin-right: $gutter;
        // }

        // @include respond-to(xs) {
        //     width: calc(100% - 40px);
        //     margin-bottom: $gutter / 2;
        //     margin-right: 40px;
        // }

        // @include respond-to(i5) {
        //     margin-right: 0;
        // }

        max-width: 320px;
        width: 100%;

        @include respond-to(sm) {
            max-width: 245px;
        }

        @include respond-to(xs) {
            max-width: 100%;
            width: calc(100% - 40px);
            margin-right: 40px;
        }

        &.pos-xs-1 {
            margin-left: auto;

            @include respond-to(xs) {
                margin-left: 0;
            }
        }

        .search {
            max-width: 100%;
            height: 40px;

            @include respond-to(xs) {
                width: 100%;
            }

            @include respond-to(i5) {
                margin: 0;
            }

            &__input {
                @extend %styleInput;

                &:hover {
                    border: 1px solid $input-color-border--hover;
                }

                &:focus {
                    font-size: 16px;
                    border: 1px solid darken($input-color-border--hover, 10%);
                    // box-shadow: $input-shadow--hover;
                }

                @include respond-to(xs) {
                    width: 100%;
                }
            } //.top-line__search .search__input
        }

        .bb-input {
            width: 100%;
        }
    } //.top-line__search

    &__date {
        margin-right: $gutter;

        @include respond-to(md) {
            margin-bottom: $gutter;
        }

        @include respond-to(xs) {
            width: calc(100% - 37px);
            margin: 0 37px 0 0;
        }
    } //.top-line__search

    &__select {

        & + & {
            margin-left: $gutter;
        }

        &.w300 {
            min-width: 300px;
        }

        @include respond-to(md) {
            margin-bottom: $gutter;
        }

        @include respond-to(xs) {
            // width: calc(50% - 5px);
            width: 100%;
            margin-bottom: $gutter / 2;
            margin-right: 0;

            & + & {
                margin-left: 0;
            }

            &:last-child {
                margin-bottom: 0;
            }

            .bb-select {
                width: 100%;
            }
        }

        @include respond-to(i5) {
            width: 100%;

            & + & {
                margin-left: 0;
            }
        }

        .bb-input--select {
            min-width: 200px;
            width: 100%;

            @include respond-to(xs) {
                min-width: 100%;
                background-color: #fff;
            }
        }
    } //.top-line__select

    &__select-box {
        @include flexCenter;

        .top-line__select {
            margin-bottom: 0;

            @include respond-to(xs) {

                select {
                    border-radius: 0;
                }

                &:not(:last-child) {

                    select {
                        border-right: 0;
                    }
                }

                &:first-child {

                    select {
                        border-top-left-radius: $border-radius;
                        border-bottom-left-radius: $border-radius;
                    }
                }

                &:last-child {

                    select {
                        border-top-right-radius: $border-radius;
                        border-bottom-right-radius: $border-radius;
                    }
                }
            }

            & + .top-line__select {
                @include respond-to(xs) {
                    margin-left: 0;
                }
            }
        }
    } //.top-line__select-box

    &__btn {
        margin-left: auto;
        flex: none;

        @include respond-to(md) {
            padding: 7px 15px;
        }

        &_controls {
            display: flex;
            width: 30px;
            line-height: 1;
            position: absolute;
            top: 6px;
            right: 0;

            @include landscape {
                display: flex !important;
            }

            @include respond-to(xs) {
                display: flex;
            }
        } //.top-line__btn_controls
    } //.top-line__btn

    .select2-selection__rendered {
        max-width: 240px;
        @include text-overflow;
    }
} //.top-line

.control-box {
    display: flex;

    @include respond-to(xs) {
        // display: none;
        width: 100%;
        flex-wrap: wrap;
    }
} //.controls-box

.adreses {

    &__list {
        @include flexColumn;
        align-items: flex-start;
    } //.adreses__list

    &__item {
        display: block;
        padding: 5px 30px 5px 0;
        position: relative;

        &:hover {

            .adreses__icon {
                display: block;
            }
        }
    } //.adreses__item

    &__link {
        font-family: $f-light;
        font-size: 14px;
        color: $color-text;
        transition: initial;

        &:hover {
            color: #000;
        }
    } //.adreses__link

    &__icon {
        display: none;
        width: 9px;
        height: 9px;
        fill: $gray--light;
        position: absolute;
        top: 0px;
        right: 15px;

        &:hover {
            fill: #000;
        }
    } //.adreses__icon
} //.adreses
