.album {
    @include respond-to(xs) {
        padding: 10px 10px 0;
    }

    &__inner {
        @include row-flex;

        .album-item {
            margin-bottom: $gutter;
            @include col;
            @include size(4);
            @include size-sm(6);
            @include size-xs(12);
        }
    } //.album__inner

    &__empty {
        margin-top: 100px;
    } //.album__empty
} //.album

.album-item {
    display: block;
    color: $color-text;
    border-radius: $border-radius;
    @extend %box;
    @include tr;
    @include nosel;

    &:hover {
        color: $color-text;
        box-shadow: 1px 1px 10px rgba(255, 130, 114, 0.6);
    }

    &__img {
        border-top-left-radius: $border-radius;
        border-top-right-radius: $border-radius;
        overflow: hidden;
        position: relative;

        @include sectionOverlay;

        &::before {
            display: none;
        }

        // @include respond-min(xs) {

        //     &:hover {

        //         &::before {
        //             display: block;
        //         }

        //         .album-item__btn {

        //             &--edit {
        //                 transform: translate(-49%, -49%);
        //                 opacity: 1;
        //             }

        //             &--dell {
        //                 opacity: 0.9;
        //             }
        //         }
        //     }
        // }
    } //.album-item__img

    &__desc {
        padding: $gutter / 2;
        position: relative;
    } //.album-item__desc

    &__title,
    &__edit {
        margin-right: 30px;
    }

    &__title {
        margin-bottom: 2px;
        font-family: $f-medium;
        font-weight: 500;
        font-size: 15px;
        @include text-overflow;
    } //.album-item__title

    &__edit {
        font-family: $f-light;
        font-weight: 300;
        font-size: 13px;
        color: #9a9a9a;

        span {
            font-family: $f-medium;
            font-weight: 500;
            font-size: 12px;
        }
    } //.album-item__edit

    &__info {
        margin-top: 10px;
        @include flexCenter;
    } //.album-item__info

    &__btn {

        &--edit {
            padding: 4px 13px;
            font-family: $f-light;
            font-size: 14px;
            border: 1px solid rgba(#fff, 0.6);
            display: flex;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-49%, 0);
            opacity: 0;
            transition: all 0.4s ease-in-out;

            i {
                margin-right: 7px;
                position: relative;
                top: 3px;
            }
        } //.album-item__btn--edit

        &--dell {
            padding: 5px 10px;
            color: #fff;
            background-color: transparent;
            border: 0;
            outline: none;
            opacity: 0;
            position: absolute;
            top: 0;
            right: 0;

            &:hover {
                opacity: 1;
                color: #fff;
            }
        } //.album-item__btn--dell
    } //.album-item__btn

    &__count {
        margin-left: auto;
        padding: 2px 10px 2px 25px;
        font-family: $f-num;
        font-size: 12px;
        text-align: center;
        color: #9a9a9a;
        border: 1px solid $color-border--lighten;
        border-radius: 15px;
        position: relative;

        &::before {
            content: '\f03e';
            @include fa('regular');
            font-size: 13px;
            @include vy;
            left: 8px;
        }
    } //.album-item__count

    &__info-block {
        position: absolute;
        right: 10px;
        top: 10px;
        z-index: z(fixed);

        .btn-icon {

            i {
                @include tr;
            }

            &:hover {

                i {
                    color: $gray--darkest;
                }
            }
        }
    } //.album-item__info-block

    &--icon {

        .album-item {

            &__desc {
                border-top: 1px solid $color-border--lightest;
            }

            &__info-block {
                @include centre('y');

                @include respond-to(xs) {
                    display: none;
                }
            }
        }
    } //.album-item--icon
} //.album-item

.preview-list {
    @include flexCenter;

    &__item {
        display: block;
        width: 28px;
        height: 28px;
        border-radius: $border-radius * 2;
        overflow: hidden;
        @include cover;

        & + & {
            margin-left: 5px;
        }
    } //.preview-list__item

    &--big {

        .preview-list {

            &__item {
                width: 32px;
                height: 32px;
            }
        }
    } //.preview--big
} //.preview-list
