$color-event-new: $color-info;
$color-event-working: $color-warning;
$color-event-done: $color-success;
$color-event-canceled: $color-danger;

$colorEvent: (
    new: $color-event-new,
    working: $color-event-working,
    done: $color-event-done,
    canceled: $color-event-canceled
);

@mixin eventColor($color) {
    border-color: $color;

    .dhx_header,
    .dhx_title,
    .dhx_body,
    .dhx_footer {
        color: $gray--darken;
        background-color: lighten($color, 33%);
    }
}

@mixin eventTitleIcon($icon, $color) {

    .dhx_title {

        &::before {
            content: $icon;
            @include fa('light');
            color: $color;

            @include respond-to(i5) {
                display: none;
            }
        }
    }
}

.dhtmlXTooltip {

    &.tooltip {
        padding: 0;
        border: 0;
        color: $color-text;
        font-family: $f;
        border-radius: $border-radius;
        box-shadow: 0 5px 30px rgba(#000, 0.4);
        z-index: 11;
    }
}

.calendar {
    padding: 0;
    background-color: #fff;

    @include respond-to(md) {
        padding-bottom: 0;
    }

    @include respond-to(xs) {
        padding: 0;
    }

    &__top-line {
        display: block;
        padding: 30px 30px 0;

        @include respond-to(md) {
            margin-bottom: 15px;
            padding: 0;
        }

        @include respond-to(xs) {
            margin-bottom: 10px;
        }

        .bb-checkbox {

            &__title {
                height: 40px;
                line-height: 33px;
                font-size: 15px;
            }
        }
    } //.calendar__top-line

    &__box-row {
        // margin-right: 40px;
        @include col;
        @include size(5);
        @include size-md(3);
        display: flex;
        justify-content: flex-start;

        @include respond-to(md) {
            // margin-right: 0;
            // margin-bottom: $gutter;

            .top-line__select {
                flex-basis: 50%;

                & + .top-line__select {
                    margin-left: 0;
                }

                &:first-child {

                    select {
                        border-radius: $border-radius 0 0 $border-radius;
                        border-right: 0;
                    }

                    &:hover,
                    &:focus {

                        & + .top-line__select {

                            select {
                                border-left-color: $input-color-border--hover;
                            }
                        }
                    }
                }

                &:last-child {

                    select {
                        border-radius: 0 $border-radius $border-radius 0;
                    }
                }

                &:only-child {

                    select {
                        border-radius: $border-radius;
                        border-right: 1px solid $input-color-border;
                    }
                }

                .bb-select {
                    max-width: 100%;
                    width: 100%;
                    margin-right: 0;
                }

                select {
                    max-width: 100%;
                    width: 100%;
                }
            }
        }

        @include respond-to(i5) {
            margin-right: 0;
        }

        // @include respond-to(xs) {
        //     // margin-right: 0;
        //     // margin-bottom: 0;
        // }
    } //.calendar__box-row

    &__date {
        // margin: auto;
        @include col;
        @include size(4);
        @include size-md(6);
        justify-content: center;

        @include respond-to(i5) {
            width: 50%;

            .bb-date__field {
                font-size: 14px;
                @include text-overflow;
            }
        }

        .bb-date {

            &__btn {

                &--today {
                    @include respond-to(sm) {
                        display: block;
                    }
                    @include respond-to(xs) {
                        display: none;
                    }
                }

                &--prev {
                    @include respond-to(xs) {
                        display: none;
                    }
                }
            }
        }

        // @include respond-to(md) {
        //     // margin: 0;
        // }
    } //.calendar__date

    &__view {
        // margin-left: 30px;
        // flex-grow: 1;
        @include col;
        @include size(3);
        justify-content: flex-end;

        @include respond-to(i5) {
            margin-left: 0;
        }

        .bb-checkbox-row {
            max-width: 300px;
            margin-left: auto;

            @include respond-to(xs) {
                max-width: 100%;
                width: 100%;
                margin-left: 0;
                margin-top: 10px;

                .bb-checkbox {
                    width: 100%;
                }
            }
        }

        .bb-select {
            width: 100%;

            select {
                width: 100%;
            }
        }
    } //.calendar__view

    &__sorting {
        @include row-flex;
        align-items: flex-start;
        // @include respond-to(md) {
        //     flex-wrap: wrap;
        //     align-items: center;
        // }

        @include respond-to(xs) {
            margin-left: 0;
            margin-right: 0;
        }

        &--small {
            @include col;
            @include size(5);
            @include size-md(3);
            display: flex;
            justify-content: flex-start;

            .calendar {

                &__box-row {
                    @include size(3);
                }

                &__date {
                    @include size(6);
                }
            }

            .top-line {

                &__select {
                    width: 100%;
                    flex-basis: 100%;

                    @include respond-to(md) {
                        min-width: 100%;
                        width: 100%;
                        flex-basis: 100%;
                    }
                }
            }
        } //.calendar__sorting--small
    } //.calendar__sorting

    .top-line {

        &__sorting {
            width: auto;
            flex-direction: row;
        }

        &__select {
            // min-width: 240px;
            width: 100%;
            margin-bottom: 0;

            @include respond-to(md) {
                min-width: auto;
                width: auto;
            }

            // @include respond-to(xs) {
            //     margin-top: 10px;
            // }
        } //.calendar .top-line__select
    }
} //.calendar

.c-calendar {
    min-height: 300px;
    height: 100%;
    position: relative;

    @include respond-to(xs) {
        margin: 0 -10px;
    }

    &__btn {
        background-color: transparent;
        border: 0;
        cursor: pointer;
        outline: none;
        position: absolute;
        top: 15px;
        z-index: 3;

        &:hover {

            i {
                color: $gray;
            }
        }

        &--prev {
            left: 45px;
        } //.c-calendar__btn--prev

        &--next {
            right: 23px;

            @include respond-to(md) {
                right: 0;
            }

            @include respond-to(xs) {
                right: 13px;
            }
        } //.c-calendar__btn--next

        i {
            font-size: 28px;
            color: $gray--lighten;
        }
    } //.c-calendar__btn

    &__timespan {
        padding-left: 5px;
        font-family: $f;
        font-size: 14px;
        background-color: #c5ccdf;
        z-index: 9;
    } //.c-calendar__timespan

    &__empty {
        padding-top: 50px;
    } //.c-calendar__empty

    .user {

        &__img {
            margin: 5px auto;
        } //.c-calendar .user __img

        &__name,
        &__surname {
            font-family: $f-medium;
            font-size: 14px;
            color: $color-text;
        } //.c-calendar .user __name, .c-calendar .user __surname
    } //.c-calendar .user

    .dhx_cal_container {
        font-family: $f;
        background-color: transparent;
        overflow: inherit;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    } //.c-calendar .dhx_cal_container

    .dhx_scale_holder {
        overflow: hidden;
    }

    .dhx_cal_header,
    .dhx_scale_bar {
        border: 0;
        background: transparent;
        font-size: 14px;
        font-family: $f-medium;
        color: $color-text;
        display: flex;
        justify-content: center;
    } //.c-calendar .dhx_cal_header, .c-calendar .dhx_scale_bar

    .dhx_cal_tab {

        &.week_tab {
            border-radius: 0 $border-radius $border-radius 0;
        }
    } //.c-calendar .dhx_cal_tab

    .dhx_cal_next_button,
    .dhx_cal_tab_last {
        border-radius: 0 $border-radius $border-radius 0;
    } //.c-calendar .dhx_cal_next_button, .c-calendar .dhx_cal_tab_last

    .dhx_cal_next_button,
    .dhx_cal_prev_button {
        top: 75px;
        border: 0;
        background: none;

        &::before {
            content: '\f105';
            @include fa('light');
            font-size: 28px;
            color: $gray--lighten;
            @include tr;
            @include centre;
        }

        &:hover {

            &::before {
                color: $gray;
            }
        }
    } //.c-calendar .dhx_cal_next_button, .c-calendar .dhx_cal_prev_button

    .dhx_cal_prev_button {
        left: 35px;

        &::before {
            content: '\f104';
        }
    } //.c-calendar .dhx_cal_prev_button

    .dhx_cal_prev_button,
    .dhx_cal_tab_first {
        border-radius: $border-radius 0 0 $border-radius;
    } //.c-calendar .dhx_cal_prev_button

    .dhx_cal_today_button {
        border-radius: $border-radius;
    } //.c-calendar .dhx_cal_today_button

    .dhx_cal_data {
        background-size: 8px 8px;
        background-image: linear-gradient(
            45deg,
            transparent 46%,
            rgba(36, 51, 74, 0.2) 49%,
            rgba(36, 51, 74, 0.2) 51%,
            transparent 55%
        );
        // background-image:  url('../libs/calendar/imgs_dhx_terrace/bb_databg_now.png');
        background-color: $color-bg--lightest;

        @include respond-to(xs) {
            padding-left: 7px;
        }

        .dhx_scale_hour {

            & > div {

                &:first-child {
                    font-family: $f-medium;
                    font-size: 14px;
                    color: $gray--dark;
                }
            }
        }
    } //.c-calendar .dhx_cal_data

    .dhx_scale_holder,
    .dhx_scale_holder_now {
        background-image: url('../libs/calendar/imgs_dhx_terrace/td_bg.png');

        &:hover {
            cursor: cell;
        }
    } //.c-calendar .dhx_scale_holder_now

    .dhx_time_reserved {
        // background-image: url('../libs/calendar/imgs_dhx_terrace/bb_databg_now.png');
        background-image: url('../libs/calendar/imgs_dhx_terrace/databg.png');
    } //.c-calendar .dhx_time_reserved

    .dhx_cal_event {
        min-height: 52px;
        margin-left: -1px;
        border-radius: $border-radius;
        z-index: 10;

        &:hover {
            cursor: pointer;
        }

        &:not(.dhx_cal_select_menu) {
            border-left: 4px solid transparent;

            .dhx_title {
                position: relative;

                &::before {
                    position: absolute;
                    right: 5px;
                }
            }

            &.event_new {
                @include eventColor($color-event-new);
                @include eventTitleIcon('\f118', $color-event-new);
            } //.dhx_cal_event.event_new

            &.event_working {
                @include eventColor($color-event-working);
                @include eventTitleIcon('\f017', $color-event-working);
            } //.dhx_cal_event.event_working

            &.event_done {
                @include eventColor($color-event-done);
                @include eventTitleIcon('\f00c', $color-event-done);
            } //.dhx_cal_event.event_done

            &.event_canceled {
                @include eventColor($color-event-canceled);
                @include eventTitleIcon('\f119', $color-event-canceled);
            } //.dhx_cal_event.event_canceled
        }

        .dhx_header,
        .dhx_title,
        .dhx_body,
        .dhx_footer {
            color: $color-text;
            background-color: #fff;
            border: 0;
            @include nosel;
        }

        .dhx_title {
            width: calc(100% - 10px);
            padding: 5px;
            font-family: $f-bold;
            font-weight: 600;
            text-align: left;
            border-top-left-radius: 0;
            border-top-right-radius: $border-radius;
        } //.c-calendar .dhx_cal_event .dhx_title

        .dhx_body {
            min-height: 23px;
            padding-top: 0;
            padding-bottom: 8px;
            border-radius: 0 0 $border-radius $border-radius;
        } //.c-calendar .dhx_cal_event .dhx_body

        .dhx_footer {
            height: 13px;
            margin-top: -13px;
        } //.c-calendar .dhx_cal_event .dhx_footer
    } //.c-calendar .dhx_cal_event //   

    .dhx_menu_head,
    .dhx_menu_icon {
        background-image: url('../libs/calendar/imgs_dhx_terrace/controls--black.png');
    } //  

    .dhx_cal_select_menu,
    .dhx_cal_editor {
        display: none;
    }
} //.c-calendar

.c-calendar-tooltip {
    @include flexColumn;

    &__header {
        padding: 5px 10px 4px;
        font-size: 14px;
        text-align: center;
        color: #fff;
        @include border(bottom);
        border-radius: $border-radius $border-radius 0 0;

        @each $theme, $color in $colorEvent {

            &.event_#{$theme} {
                background-color: $color;
            }
        }

        // &.event_new {
        //     background-color: $color-event-new;
        // }

        // &.event_working {
        //     background-color: $color-event-working;
        // }

        // &.event_done {
        //     background-color: $color-event-done;
        // }

        // &.event_canceled {
        //     background-color: $color-event-canceled;
        // }
    }

    &__body {
        padding: 5px 10px;
        @include flexColumn;

        & > span {

            &:not(:first-child) {
                padding-top: 5px;
                @include border(top);
            }

            & + span {
                margin-top: 5px;
            }
        }

        @each $theme, $color in $colorEvent {

            &.event_#{$theme} {

                .c-calendar-tooltip__status {
                    color: $color;
                    border-color: $color;
                }
            }
        }
    }

    &__user {
        font-family: $f-medium;
        font-size: 14px;
    }

    &__status {
        margin-left: 15px;
        padding: 0 7px;
        font-size: 11px;
        border: 1px solid $color-border--lightest;
        border-radius: 15px;
    }

    &__phone,
    &__time {
        font-family: $f-num--m;
    }
} //.c-calendar-tooltip
