.chart__wrapper {
    line-height: 0;
} //.chart__wrapper

.domain {
    stroke: transparent;
} //.domain

.tick line {
    stroke: transparent;
} //.tick

.textLegend {
    font-size: 15px;

    @include respond-min(sm) {
        font-family: $f-light;
    }

    @include respond-to(sm) {
        font-size: 16px;
    }
} //.textLegend

.pieValue {
    font-size: 14px;

    @include respond-to(xs) {
        font-family: $f-medium;
    }
} //pieVale

.pieText {
    font-size: 14px;

    @include respond-to(xs) {
        font-family: $f-medium;
    }
}

.axis--x .tick {

    &:hover {
        color: $color-main;
        cursor: pointer;
    }
} //.axis--x
